  methods: {
    //
    clickImg(e) {
      var el = e.target;
      if (el.tagName == "IMG") {
        this.currentImg = e.target;
        this.imgForm.height = el.height;
        this.imgForm.width = el.width;
        el.title = "";
        this.imgFormVisible = true;
      }
    },
    //
    imgEdited() {
      var l = parseFloat(this.imgForm.height);
      var w = parseFloat(this.imgForm.width);
      this.currentImg.width = w;
      this.currentImg.height = l;
      this.imgFormVisible = false;
    },
    //
    handleCurrentChange(val) {
      this.page = val;
      this.search();
    },
    //
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      console.log(file);
      let that = this;
      let type = file.name.split(".");
      type = type.pop(); //
      console.log(type);
      // let params = Qs.stringify({
      //     imgs: file,
      //   });
      let formData = new FormData();
      let url;
      if (
        type.indexOf("png") > -1 ||
        type.indexOf("jpg") > -1 ||
        type.indexOf("jpeg") > -1 ||
        type.indexOf("gif") > -1 ||
        type.indexOf("pdf") > -1
      ) {
        formData.append("imgs", file);
        url = that.HOST + "/common/uploadImg";
      } else {
        formData.append("videos", file);
        url = that.HOST + "/common/uploadVideo";
      }
      that
        .axios({
          url: url,
          method: "POST",
          headers: { "Content-Type": "multipart/form-data" },
          data: formData
        })
        .then(response => {
          if (response.data.result == "success") {
            let url = response.data.message; // Get url from response
            if (
              type.indexOf("png") > -1 ||
              type.indexOf("jpg") > -1 ||
              type.indexOf("jpeg") > -1 ||
              type.indexOf("gif") > -1 ||
              type.indexOf("pdf") > -1
            ) {
              Editor.insertEmbed(cursorLocation, "image", url[0]); //
            } else {
              Editor.insertEmbed(cursorLocation, "video", url[0]); //
            }
            // resetUploader();
          } else {
            that.$message.error({
              showClose: true,
              message: "",
              duration: 2000
            });
          }
        })
        .catch(err => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "",
            duration: 2000
          });
        });
    },
    //
    beforeAvatarUpload(file) {
      // const isJPG = file.type === 'image/jpeg';
      const isLt10M = file.size / 1024 / 1024 < 10;
      // if (!isJPG) {
      //   this.$message.error(' JPG !');
      // }
      if (!isLt10M) {
        this.$message.error(" 10MB!");
      }
      // return isJPG && isLt10M;
    },
    //
    //
    showAddDialog() {
      this.addFormVisible = true;
      this.addForm = {
        name: "",
        introduction: ""
      };
    },
    //
    addImgUpload(e) {
      let that = this;
      // let params = Qs.stringify({
      //     imgs: file,
      //   });
      let url = that.HOST + "/common/uploadImg";
      let formData = new FormData();
      formData.append("imgs", e.file);
      that
        .axios({
          url: url,
          method: "POST",
          headers: { "Content-Type": "multipart/form-data" },
          data: formData
        })
        .then(response => {
          if (response.data.result == "success") {
            that.$message.success({
              showClose: true,
              message: "",
              duration: 2000
            });
            that.addCoverShow = true;
            that.addForm.img = response.data.message[0];
          } else {
            that.$message.error({
              showClose: true,
              message: "",
              duration: 2000
            });
          }
        })
        .catch(err => {
          console.log(err);
