            message: "",
            duration: 2000
          });
        });
    },
    //
    addSubmit() {
      let that = this;
      let para = Object.assign({}, this.addForm);
      var d = new Date(para.postDate);
      var postDate = getTime(d);
      if (para.name && para.introduction) {
        that.$confirm("", "", { type: "warning" }).then(() => {
          that.addForm.introduction = that.addForm.introduction
            .replace(/<\/div>/g, "</p>")
            .replace(/<div/g, "<p"); //vue2-editorv-modeldiv
          let url = that.HOST + "/introduction/manager/addIntroduction";
          let params = Qs.stringify({
            name: para.name,
            // source: para.source,
            postDate: postDate,
            img: para.img,
            introduction: para.introduction,
            categoryId: that.categoryId
          });
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              that.addCoverShow = true;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.addFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.addFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "",
          duration: 1000
        });
      }
    },
    //
    delItem(index, row) {
      let that = this;
      this.$confirm("?", "", { type: "warning" })
        .then(() => {
          that.loading = true;
          let postId = row.id;

          let url = that.HOST + "/introduction/manager/deleteIntroduction";
          let params = Qs.stringify({
            ids: postId
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        })
        .catch(() => {});
