      this.sels = sels;
    },
    //
    batchDeleteItems() {
      let that = this;
      this.$confirm("", "", {
        type: "warning"
      })
        .then(() => {
          that.loading = true;
          let ids = that.sels.map(item => item.id).toString();
          let url = that.HOST + "/introduction/manager/deleteIntroduction";
          let params = Qs.stringify({
            ids: ids
          });
          that
