      let that = this;
      let para = Object.assign({}, this.editForm);
      var d = new Date(para.postDate);
      var postDate = getTime(d);
      if (para.name && para.introduction) {
        that.$confirm("", "", { type: "warning" }).then(() => {
          let url = that.HOST + "/introduction/manager/updateIntroduction";
          let params = Qs.stringify({
            id: para.id,
            name: para.name,
            // source: para.source,
            postDate: postDate,
            img: para.img,
            introduction: para.introduction
            // categoryId: that.categoryId
          });
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.editFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.editFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "",
          duration: 1000
        });
      }
    }
  },
  activated() {
    this.handleSearch();
  },
  watch: {
    //
    isRequested() {
      this.handleSearch();
      console.log(this.isRequested);
    }
  }
};
</script>
<style lang="scss">
.ql-snow .ql-editor code,
.ql-snow .ql-editor pre {
  background-color: #ffffff !important;
}
.el_b_form input {
  width: 50% !important;
}
.el-dialog .editor1 #quill-container {
  max-height: 1000px;
  overflow: auto;
}
.ql-snow .ql-picker {
  height: auto !important;
  margin-top: -6px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 108px;
  height: 108px;
  line-height: 108px;
  text-align: center;
}
.avatar {
  width: 108px;
  height: 108px;
  display: block;
}
.avatar2 {
  @extend .avatar;
}
.ql-snow .ql-tooltip {
  left: 23.602px !important;
  top: 31px !important;
  color: green;
}
.ql-snow .ql-editor img {
  cursor: pointer;
}
</style>




