<template>
  <div id="prodEdit">
    <el-row class="warp">
      <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input v-model="filters.content" placeholder="" @keyup.enter.native="handleSearch"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="handleSearch"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="showAddDialog"></el-button>
            </el-form-item>
          </el-form>
        </el-col>

        <!---->
        <el-table :data="items" highlight-current-row @selection-change="selsChange" style="width: 100%; margin-bottom:10px;">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column type="index" width="60"></el-table-column>
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="[]">
                  <img :src="props.row.img" alt="">
                  <div v-html="props.row.introduction" style="display:block;"></div>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column prop="name" label="" sortable></el-table-column>
          <el-table-column prop="price" label="" width="100" sortable :formatter="formatPrice"></el-table-column>
          <el-table-column prop="lastUpdateTime" label="" width="150" sortable></el-table-column>
          <el-table-column label="" width="200">
            <template slot-scope="scope">
              <el-button size="small" @click="showEditDialog(scope.$index,scope.row)"></el-button>
              <el-button type="danger" @click="delItem(scope.$index,scope.row)" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>

        <!---->
        <el-col :span="24" class="toolbar">
          <el-button type="danger" @click="batchDeleteItems" :disabled="this.sels.length===0"></el-button>
          <el-pagination layout="prev, pager, next" background @current-change="handleCurrentChange" :page-size="pageSize" :total="total" style="float:right;">
          </el-pagination>
        </el-col>

        <!--  -->
        <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="true" top="8vh" :modal-append-to-body="false">
          <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
            <el-form-item label="" prop="name">
              <el-input v-model="editForm.name" auto-complete="off"></el-input>
            </el-form-item>
            <!-- <el-form-item label="" prop="price">
              <el-input v-model="editForm.price" auto-complete="off"></el-input>
            </el-form-item> -->
            <el-form-item label="" prop="lastUpdateTime">
              <el-date-picker type="date" placeholder="" v-model="editForm.lastUpdateTime"></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="img">
              <el-upload class="avatar-uploader" action="http://122.13.4.239/common/uploadImg" :http-request="editImgUpload" :auto-upload="true" :show-file-list="false" :before-upload="beforeAvatarUpload">
                <img v-if="editCoverShow" :src="editForm.img" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="" prop="introduction">
              <div @click="clickImg($event)">
                <vue-editor class="editor1" :editorToolbar="customToolbar" v-model="editForm.introduction" useCustomImageHandler @imageAdded="handleImageAdded"></vue-editor>
              </div>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="editFormVisible = false"></el-button>
            <el-button type="primary" @click.native="editSubmit"></el-button>
          </div>
        </el-dialog>

        <!---->
        <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="true" top="8vh" :modal-append-to-body="false">
          <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
            <el-form-item label="" prop="name">
              <el-input v-model="addForm.name" auto-complete="off"></el-input>
            </el-form-item>
            <!-- <el-form-item label="" prop="price">
              <el-input v-model="addForm.price" auto-complete="off"></el-input>
            </el-form-item> -->
            <el-form-item label="" prop="lastUpdateTime">
              <el-date-picker type="date" placeholder="" v-model="addForm.lastUpdateTime"></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="img">
              <el-upload class="avatar-uploader" action="http://122.13.4.239/common/uploadImg" :http-request="addImgUpload" :auto-upload="true" :show-file-list="false" :before-upload="beforeAvatarUpload">
                <img v-if="addCoverShow" :src="editForm.img" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="" prop="introduction">
              <div @click="clickImg($event)">
                <vue-editor class="editor1" :editorToolbar="customToolbar" v-model="addForm.introduction" useCustomImageHandler @imageAdded="handleImageAdded"></vue-editor>
              </div>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="addFormVisible = false"></el-button>
            <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
          </div>
        </el-dialog>
      </el-col>
    </el-row>
    <el-dialog title="" :visible.sync="imgFormVisible" width="30%" top="8vh" :close-on-click-modal="true" :modal-append-to-body="false">
      <el-form :model="imgForm">
        <el-form-item label="(px)">
          <el-input v-model="imgForm.height" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="(px)">
          <el-input v-model="imgForm.width" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="imgFormVisible = false"> </el-button>
        <el-button type="primary" @click.native="imgEdited" :loading="imgLoading"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import API from "@/api/api_admin";
import Qs from "qs";
import { VueEditor } from "vue2-editor";

export default {
  name: "prdEdit",
  components: { VueEditor },
  props: ["currentTab", "isRequested"],
  data() {
    return {
      categoryId: 0,
      //
      filters: {
        content: ""
      },
      //
      items: [
        {
          id: "",
          name: "",
          lastUpdateTime: "2018-08-20",
          price: "",
          introduction: "asdasdasd"
        }
      ],
      total: 1, //
      pageSize: 12, //
      page: 1, //
      // limit: 10,
      loading: false, //loading  
      sels: [], //
      //
      //categoryId
      initFormRules: [],
      customToolbar: [
        // [{ 'size': ['small', false, 'large', 'huge'] }],
        [{ header: [false, 1, 2, 3, 4, 5, 6] }],
        ["bold", "italic", "underline", "strike"],
        ["clean"],
        [
          { align: "" },
          { align: "center" },
          { align: "right" },
          { align: "justify" }
        ],
        [{ list: "ordered" }, { list: "bullet" }],
        [{ indent: "-1" }, { indent: "+1" }],
        ["blockquote", "code-block"],
        ["image", "link"]
      ],
      //
      editCoverShow: false, //
      editFormVisible: false, //
      // 
      editFormRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        // price: [{ required: false, message: "", trigger: "blur" }],
        introduction: [
          { required: false, message: "", trigger: "blur" }
        ],
        img: [{ required: false, message: "", trigger: "blur" }]
        // lastUpdateTime: [
        //   { required: true, message: "", trigger: "blur" }
        // ]
      },
      //refdom
      editForm: {
        id: 0,
        name: "",
        // price: "",
        // lastUpdateTime: "",
        introduction: "asdasdasd",
        img: ""
      },
      //
      addFormVisible: false, //
      addCoverShow: false,
      addLoading: false,
      // 
      addFormRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        // price: [{ required: false, message: "", trigger: "blur" }],
        introduction: [
          { required: false, message: "", trigger: "blur" }
        ],
        img: [{ required: false, message: "", trigger: "blur" }]
        // lastUpdateTime: [
        //   { required: true, message: "", trigger: "blur" }
        // ]
      },
      //refdom
      addForm: {
        name: "",
        // price: "",
        // lastUpdateTime: "",
        introduction: "asdasdasd",
        img: ""
      },
      //
      currentImg: new Object(),
      imgFormVisible: false, //
      //
      imgForm: {
        height: "",
        width: ""
      },
      imgLoading: false
    };
  },
  methods: {
    formatPrice: function(row, column) {
      return row.price == 0 ? "" : row.price;
    },
    //
    clickImg(e) {
      var el = e.target;
      if (el.tagName == "IMG") {
        this.currentImg = e.target;
        this.imgForm.height = el.height;
        this.imgForm.width = el.width;
        el.title = "";
        this.imgFormVisible = true;
      }
    },
    //
    imgEdited() {
      var l = parseFloat(this.imgForm.height);
      var w = parseFloat(this.imgForm.width);
      this.currentImg.width = w;
      this.currentImg.height = l;
      this.imgFormVisible = false;
    },
    //
    handleCurrentChange(val) {
      this.page = val;
      this.search();
    },
    //
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      let that = this;
      let type = file.name.split(".");
      type = type.pop(); //
      // let params = Qs.stringify({
      //     imgs: file,
      //   });
      let formData = new FormData();
      let url;
      if (
        type.indexOf("png") > -1 ||
        type.indexOf("jpg") > -1 ||
        type.indexOf("jpeg") > -1 ||
        type.indexOf("gif") > -1 ||
        type.indexOf("pdf") > -1
      ) {
        formData.append("imgs", file);
        url = that.HOST + "/common/uploadImg";
      } else {
        formData.append("videos", file);
        url = that.HOST + "/common/uploadVideo";
      }
      that
        .axios({
          url: url,
          method: "POST",
          headers: { "Content-Type": "multipart/form-data" },
          data: formData
        })
        .then(response => {
          if (response.data.result == "success") {
            let url = response.data.message; // Get url from response
            if (
              type.indexOf("png") > -1 ||
              type.indexOf("jpg") > -1 ||
              type.indexOf("jpeg") > -1 ||
              type.indexOf("gif") > -1 ||
              type.indexOf("pdf") > -1
            ) {
              Editor.insertEmbed(cursorLocation, "image", url[0]); //
            } else {
              Editor.insertEmbed(cursorLocation, "video", url[0]); //
            }
            // resetUploader();
          } else {
            that.$message.error({
              showClose: true,
              message: "",
              duration: 2000
            });
          }
        })
        .catch(err => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "",
            duration: 2000
          });
        });
    },
    //
    beforeAvatarUpload(file) {
      // const isJPG = file.type === 'image/jpeg';
      const isLt10M = file.size / 1024 / 1024 < 10;
      // if (!isJPG) {
      //   this.$message.error(' JPG !');
      // }
      if (!isLt10M) {
        this.$message.error(" 10MB!");
      }
      // return isJPG && isLt10M;
    },
    //
    //
    showAddDialog() {
      this.addFormVisible = true;
      this.addForm = {
        name: "",
        introduction: ""
      };
    },
    //
    addImgUpload(e) {
      let that = this;
      // let params = Qs.stringify({
      //     imgs: file,
      //   });
      let url = that.HOST + "/common/uploadImg";
      let formData = new FormData();
      formData.append("imgs", e.file);
      that
        .axios({
          url: url,
          method: "POST",
          headers: { "Content-Type": "multipart/form-data" },
          data: formData
        })
        .then(response => {
          if (response.data.result == "success") {
            that.$message.success({
              showClose: true,
              message: "",
              duration: 2000
            });
            that.addCoverShow = true;
            that.addForm.img = response.data.message[0];
          } else {
            that.$message.error({
              showClose: true,
              message: "",
              duration: 2000
            });
          }
        })
        .catch(err => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "",
            duration: 2000
          });
        });
    },
    //
    addSubmit() {
      let that = this;
      let para = Object.assign({}, this.addForm);
      if (para.name) {
        that.$confirm("", "", { type: "warning" }).then(() => {
          that.addForm.introduction = that.addForm.introduction
            .replace(/<\/div>/g, "</p>")
            .replace(/<div/g, "<p"); //vue2-editorv-modeldiv
          let url = that.HOST + "/product/manager/addProduct";
          let params = Qs.stringify({
            name: para.name,
            // price: para.price,
            lastUpdateTime: para.lastUpdateTime,
            introduction: para.introduction,
            img: para.img
          });
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.addFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.addFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "",
          duration: 1000
        });
      }
    },
    //
    delItem(index, row) {
      let that = this;
      this.$confirm("?", "", { type: "warning" })
        .then(() => {
          that.loading = true;
          let postId = row.id;

          let url = that.HOST + "/product/manager/deleteProduct";
          let params = Qs.stringify({
            ids: postId
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        })
        .catch(() => {});
    },
    //
    selsChange: function(sels) {
      this.sels = sels;
    },
    //
    batchDeleteItems() {
      let that = this;
      this.$confirm("", "", {
        type: "warning"
      })
        .then(() => {
          that.loading = true;
          let ids = that.sels.map(item => item.id).toString();
          let url = that.HOST + "/product/manager/deleteProduct";
          let params = Qs.stringify({
            ids: ids
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        })
        .catch(() => {});
    },
    //
    handleSearch() {
      this.total = 0;
      this.page = 1;
      this.search();
    },
    //
    search() {
      //   console.log(this.currentTab);
      let that = this;

      let url = that.HOST + "/common/search";
      let content = that.filters.content.trim();

      // if (that.categoryId != "") {
      let data = {
        pageNum: that.page, //1
        content: content,
        channel: 2
      };
      //loading
      that.loading = true;
      //get
      that.axios
        .get(url, {
          params: data
        })
        .then(response => {
          that.items = response.data.message.results.list;
          that.items.forEach((item, index) => {
            if (item.lastUpdateTime)
              that.items[index].lastUpdateTime = item.lastUpdateTime.slice(
                0,
                10
              );
          });
          that.total = response.data.message.results.total;
          that.pageSize = response.data.message.results.pageSize;
          that.loading = false;
        })
        .catch(response => {
          that.loading = false;
          that.items = [];
          that.total = 0;
          that.$message.error({
            showClose: true,
            message: "",
            duration: 2000
          });
        });
    },

    //
    //
    showEditDialog(index, row) {
      this.editFormVisible = true;
      if (row.img) {
        this.editCoverShow = true;
      }
      this.editForm = Object.assign({}, row);
      this.editForm.introduction = this.editForm.introduction
        .replace(/<\/div>/g, "</p>")
        .replace(/<div/g, "<p"); //vue2-editorv-modeldiv
    },
    //
    editImgUpload(e) {
      let that = this;
      // let params = Qs.stringify({
      //     imgs: file,
      //   });
      let url = that.HOST + "/common/uploadImg";
      let formData = new FormData();
      formData.append("imgs", e.file);
      that
        .axios({
          url: url,
          method: "POST",
          headers: { "Content-Type": "multipart/form-data" },
          data: formData
        })
        .then(response => {
          if (response.data.result == "success") {
            that.editCoverShow = true;
            that.editForm.img = response.data.message[0];
          } else {
            that.$message.error({
              showClose: true,
              message: "",
              duration: 2000
            });
          }
        })
        .catch(err => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "",
            duration: 2000
          });
        });
    },
    //
    editSubmit() {
      let that = this;
      let para = Object.assign({}, this.editForm);
      if (para.name) {
        that.$confirm("", "", { type: "warning" }).then(() => {
          let url = that.HOST + "/product/manager/updateProduct";
          let params = Qs.stringify({
            id: para.id,
            name: para.name,
            // price: para.price,
            lastUpdateTime: para.lastUpdateTime,
            introduction: para.introduction,
            img: para.img
          });
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.editFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.editFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "",
          duration: 1000
        });
      }
    }
  },
  activated() {
    this.handleSearch();
  },
  watch: {
    //
    isRequested() {
      this.handleSearch();
      // console.log(this.isRequested);
    }
  }
};
</script>
<style lang="scss" scoped>
</style>



